import React, { Component } from 'react';
import { ComponentLeader, Loader } from '../widgets';
import Particles from 'react-particles-js';

import{ YeahModal,SalesModal } from '../widgets/modals';
const features = require('./data').default;

class ShortTermIndexPage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            showModal: false
        }
    }

    componentDidMount() {
        (function(){
		'use strict';

		var items = document.querySelectorAll(".timeline li");

		function isElementInView(el){
			var rect = el.getBoundingClientRect();
			return(
			rect.top >= 0&&
			rect.left >= 0 &&
			rect.bottom <=(window.innerHeight||document.documentElement.clientHeight)&&
			rect.right <=(window.innerWidth||document.documentElement.clientWidth)
			);
		}

		function callbackFunc(){
			for(var i=0;i<items.length;i++)
			{
				if (isElementInView(items[i])){
					items[i].classList.add("in-view");
				}
			}
		}

		window.addEventListener("load",callbackFunc);
		window.addEventListener("resize",callbackFunc);
		window.addEventListener("scroll",callbackFunc);

	})();
    }

    openSalesModal(){
        this.setState({
            showSalesModal: true
        })
    }

    render() {
        return (
            <div>
                <ComponentLeader title="Short-Term Service"/>
                <div className="short-term">
                    <div className="container-fluid">
                        {this.renderTimeline()}
                        <div className="features">
                            <div className="row">
                                {this.renderFeatures()}                      
                            </div>
                        </div>
                        {this.renderTable()}
                        {this.renderEnrollNow()}
                        {this.state.showSalesModal && <YeahModal><SalesModal /></YeahModal>}
                    </div>
                </div>
            </div>
            
        )
    }

    renderTimeline() {
        return (
            <div className="timeline">
                <ul>
                    <h1>Service Process</h1>
                    <li><div>
                        <h2>8月份</h2>
                        <h3>第一次会面</h3>
                        <h5>· 填寫個人背景故事</h5>
                        <h5>· 挑選PS主題</h5>
                    </div></li>
                    <li><div>
                        <h2>9月份</h2>
                        <h3>完成PS初稿</h3>
                        <h5>· 討論初稿故事細節</h5>
                        <h5>· 潤色PS文字表達</h5>
                        <h5>· 深入思考PS主旨</h5>            
                    </div></li>

                    <li><div>
                        <h2>10月份</h2>
                        <h3>完成PS第二稿</h3>
                        <h5>· 潤色PS文字表達</h5>
                        <h5>· 歐耶總監審閱文章</h5>
                    </div></li>	

                    <li><div>
                        <h2>11月份</h2>
                        <h3>完成PS最終稿</h3>
                        <h5>· 填寫申請表格</h5>
                        <h5>· 参加申請講座</h5>
                    </div></li>

                    <li><div>
                        <h1>成功转学</h1>
                    </div></li>	
                    <br />
            </ul>
            </div>
        );
    }

    renderFeatures() {
        if (features) {
            return features.map((feature, index) => {
                return (
                    <div className="col-xs-6 col-md-4" key={index}>
                        <div id="image">
                            <img src={feature.link}/>                                    
                        </div>
                        <span>
                            <h3>{feature.title}</h3>
                            <p>{feature.description}</p>
                        </span>
                    </div>
                );
            });
        } else {
           return <Loader />
        }
    }

    renderTable() {
        return (
            <div className="table">
                <div className="row">
                    <div className="col-xs-3">
                        <div className="table-cell"></div>
                        <span>
                            <div className="table-cell">
                                <h5>Target School</h5>
                            </div>
                            <div className="table-cell">
                                <h5>背景了解规划</h5>
                            </div>
                            <div className="table-cell">
                                <h5>深度素材挖掘</h5>
                            </div>
                            <div className="table-cell">
                                <h5>1对1视频探讨</h5>
                            </div>
                            <div className="table-cell">
                                <h5>文书提升润色</h5>
                            </div>
                            <div className="table-cell">
                                <h5>申请表格指导</h5>
                            </div>
                            <div className="table-cell">
                                <h5>总监私人服务</h5>
                            </div>
                        </span>                        
                    </div>
                    <div className="col-xs-3">
                        <div className="table-cell service-name-1">代写套餐</div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">普通UC</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell unchecked"></div>
                            <div className="table-cell unchecked"></div>
                        </span>                        
                    </div>
                    <div className="col-xs-3">
                        <div className="table-cell service-name-2">尊享套餐</div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">UCB/UCLA</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell unchecked"></div>
                        </span>                        
                    </div>
                    <div className="col-xs-3">
                        <div className="table-cell service-name-3">VIP套餐</div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">UCB/私立大学</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                        </span>                        
                    </div>
                </div>
            </div>
        );
    }

    renderEnrollNow(){
            return (
                <div className="take_actions">
                    <Particles/>
                    <div className="card">
                        <div className="title">
                            實現你的夢想
                        </div>
                        <div className="action_button" onClick={this.openSalesModal.bind(this)}>
                            聯繫我們
                        </div>
                        <div className="description">
                            夢想永不止步
                        </div>
                    </div>
                </div>
            )
        }

}

export default ShortTermIndexPage;


